




























import { Component, Vue } from 'vue-property-decorator'

@Component
export default class PersonalInfo extends Vue {
  isAnimate = true
  imageUrl = 'https://res.cloudinary.com/dq4ljorlq/image/upload/v1545406485/profile/32645619_1151856074951834_1994291248407511040_n_1.jpg'

  animate () {
    this.isAnimate = false
    setTimeout(() => {
      this.isAnimate = true
    }, 100)
  }
}
